@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

.App-Nav-Hero {
    background-image: url('./images/bg-main.png'),linear-gradient(0deg, rgba(150,25,56,1) 49%, rgba(106,17,66,1) 81%);
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    background-size: 100% 100%;
    background-position: center;
  }


.font-righteous{
    font-family: 'Righteous', cursive;
}

.font-fredoka{
    font-family: 'Fredoka One', cursive;
}

.Appbar {
background-color: rgba(255, 255, 255, 0);
box-shadow: none;
position: relative;
background-image: repeating-linear-gradient(-45deg, rgba(255,255,255, 0.1), rgba(255,255,255, 0.1) 1px, transparent 1px, transparent 6px);
background-size: 4px 4px;
}



.Navbutton{
    display: none;
}

.NavbuttonMult{
    width: 100%;
    display: flex;
    justify-content: end;    
}

@media (max-width: 1052px) {
    .Navbutton{
        width: 100%;
        display: flex;
        justify-content: end;
    }

    .NavbuttonMult{
        display: none;
    }
}

.Nav-Hero-Mobile{
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: auto;
}

@media (max-width: 1052px) {
    .Nav-Hero-Mobile{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: auto;
    }
}

.Hero{
    height: 100vh;
    display: flex;
    flex-direction: row;
    width: 100%;
    }